import React from 'react'
import {Container} from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './worktools.css'
// import { faReact } from '@fortawesome/free-solid-svg-icons';

const Worktools = () =>{
 return( 
    <Container>   
    <div id="work-tools">
        <h3 className="work-tools">Work Tools</h3>
    </div>
    <div>
    <i className="fab fa-react fa-5x icons" ></i>
    <i className="fab fa-js fa-5x icons"></i>
    <i className="fab fa-html5 fa-5x icons"></i>
    <i className="fab fa-css3-alt fa-5x icons"></i>
    <i className="fab fa-github fa-5x icons"></i>
    <i className="fab fa-node-js fa-5x icons"></i>
    {/* Favorite Food: <FontAwesomeIcon icon={faReact} /> */}
  </div>
    </Container>   

    )    
}

export default Worktools