import React from 'react'
import './contact.css'

const Contact = () => {
return(
    <footer id="contact" className="contact-container">
        <i className="fas fa-envelope contact-i fa-2x"></i>
        <i className="fab fa-github contact-i fa-2x"></i>
        <i className="fab fa-linkedin-in contact-i fa-2x"></i>
        <i className="fab fa-twitter contact-i fa-2x"></i>
    </footer>
)
}
export default Contact