import React from 'react'
import './skills.css'
import { Container } from 'reactstrap'

const Skills = () => {
return(
     <Container>   
    <div id="skills">
        <h3 className="skills">Skills</h3>
    </div>
    <ul>
        <li>Team work</li>
        <li>Agile Methodology</li>
        <li>Word facility</li>
        <li>Autodidactism</li>
        <li>Creativity</li>
        <li>Empathy</li>
    </ul>
    </Container> 
)
}
export default Skills

